$tablet-min-width: 640px;
$desktop-min-width: 1200px;

@mixin on-tablet {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin only-mobile {
  @media (max-width: calc($tablet-min-width - 1px)) {
    @content;
  }
}
